$logo_size: 40px;
$link_color_hover: #00acdc;

@mixin reset_indent {
  padding: 0;
  margin: 0;
}

@mixin link_info_size {
  font-size: 12px;
  font-weight: 500;
}

@mixin star_properties {
  height: 15px;
  width: 15px;
  background: url(./../images/star.svg) no-repeat center;
  margin-right: 4px;
}
